* {
  margin: 0;
}

:root {
  --empty-star: url(./images/star.svg);
  --full-star: url(./images/star-active.svg);
}

.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  background: var(--empty-star) no-repeat center;
}

.stars .stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n + 1),
.stars--2 .stars__star:nth-child(-n + 2),
.stars--3 .stars__star:nth-child(-n + 3),
.stars--4 .stars__star:nth-child(-n + 4),
.stars--5 .stars__star:nth-child(-n + 5) {
  background: var(--full-star) no-repeat center;
}
